/* global grist */
import type { GristOptions, GristRecord, GristColumn } from './types';

type RecordsListener = (data: GristRecord[]) => void;
type SchemaListener = (columns: GristColumn[]) => void;
type OptionsListener = (opts: GristOptions) => void;

let currentOptions: GristOptions = { theme: 'light', locale: 'en-US' };

export function initGrist(
  onRecords: RecordsListener,
  onSchema: SchemaListener,
  onOptions: OptionsListener
) {
  if (typeof (window as any).grist === 'undefined') {
    console.warn('Grist API not found; running in standalone mode.');
    onRecords([]);
    onSchema([]);
    onOptions(currentOptions);
    return;
  }

  grist.ready({ allowSelectBy: false });

  grist.onRecords((records: GristRecord[], mappings?: any) => {
    onRecords(records || []);
  });

  // Grist may not push full schema; we try via docApi
  if (grist.docApi) {
    grist.docApi.fetchTable(grist.getTableId?.()).then((table: any) => {
      const columns: GristColumn[] = (table?.columns || []).map((c: any) => ({
        id: c.id,
        name: c.label || c.colId || c.name,
        type: c.type || 'Text'
      }));
      onSchema(columns);
    }).catch(() => onSchema([]));
  } else {
    onSchema([]);
  }

  grist.onOptions((opts: any) => {
    currentOptions = {
      ...currentOptions,
      ...(opts || {})
    };
    onOptions(currentOptions);
  });
}

export function setOptions(opts: Partial<GristOptions>) {
  currentOptions = { ...currentOptions, ...opts };
  if (typeof (window as any).grist !== 'undefined') {
    grist.setOption(currentOptions);
  }
}

export function getOptions(): GristOptions {
  return currentOptions;
}
