import type { GristColumn } from './types';

type SemanticType = 'nominal' | 'ordinal' | 'quantitative' | 'temporal';
type AnalyticType = 'dimension' | 'measure';

export interface GWField {
  name: string;
  semanticType: SemanticType;
  analyticType: AnalyticType;
}

const numericTypes = new Set(['Numeric', 'Int', 'Decimal', 'Float']);
const temporalTypes = new Set(['Date', 'DateTime', 'Time']);

export function inferGWField(col: GristColumn): GWField {
  const t = (col.type || '').trim();
  if (numericTypes.has(t)) {
    return {
      name: col.name,
      semanticType: 'quantitative',
      analyticType: 'measure'
    };
  }
  if (temporalTypes.has(t)) {
    return {
      name: col.name,
      semanticType: 'temporal',
      analyticType: 'dimension'
    };
  }
  // Default to nominal dimension
  return {
    name: col.name,
    semanticType: 'nominal',
    analyticType: 'dimension'
  };
}

export function buildGWFields(columns: GristColumn[]): GWField[] {
  return columns.map(inferGWField);
}
