import React, { useEffect, useMemo, useState } from 'react';
import { GraphicWalker } from '@kanaries/graphic-walker';
import { initGrist, setOptions, getOptions } from './gristBridge';
import { buildGWFields } from './fields';
import type { GristRecord, GristColumn, GristOptions } from './types';

// Always normalize safely
function normalizeData(records: GristRecord[] = []): Record<string, any>[] {
  return (records || []).map(r => {
    const o: Record<string, any> = {};
    for (const k of Object.keys(r || {})) {
      const v = (r as any)[k];
      o[k] = v === undefined ? null : v;
    }
    return o;
  });
}

export default function App() {
  const [records, setRecords] = useState<GristRecord[]>([]);
  const [columns, setColumns] = useState<GristColumn[]>([]);
  const [options, setLocalOptions] = useState<GristOptions>(getOptions());

  useEffect(() => {
    initGrist(
      (recs) => setRecords(recs || []),
      (cols) => setColumns(cols || []),
      (opts) => setLocalOptions(opts || getOptions())
    );
  }, []);

  const data = useMemo(() => normalizeData(records || []), [records]);
  const fields = useMemo(() => buildGWFields(columns || []), [columns]);

  const locale = options.locale || 'en-US';

  // Debugging logs to confirm arrays are valid
  console.log("Data length:", data.length, "Fields length:", fields.length);

  return (
    <div className="app">
      <div className="toolbar">
        <span className="badge">Grist table rows: {records.length}</span>
        <label>
          <span style={{ marginRight: 6 }}>Theme</span>
          <select
            className="select"
            value={options.theme || 'light'}
            onChange={(e) => {
              const next = { ...options, theme: e.target.value as 'light' | 'dark' };
              setLocalOptions(next);
              setOptions(next);
            }}
          >
            <option value="light">Light</option>
            <option value="dark">Dark</option>
          </select>
        </label>
        <label style={{ marginLeft: 12 }}>
          <span style={{ marginRight: 6 }}>Locale</span>
          <select
            className="select"
            value={locale}
            onChange={(e) => {
              const next = { ...options, locale: e.target.value };
              setLocalOptions(next);
              setOptions(next);
            }}
          >
            <option value="en-US">en-US</option>
            <option value="fr-FR">fr-FR</option>
            <option value="de-DE">de-DE</option>
            <option value="ja-JP">ja-JP</option>
            <option value="zh-CN">zh-CN</option>
          </select>
        </label>
      </div>
      <div className="content">
        <GraphicWalker
          data={data || []}
          fields={fields || []}
          i18nLang={locale}
          themeKey={options.theme === 'dark' ? 'dark' : 'light'}
        />
      </div>
    </div>
  );
}
